/* eslint-disable no-undef */
import React from 'react'
import { addFieldMap } from './IntegrationHelpers/IntegrationHelpers'
import { refreshFields, refreshOrganizations } from './ZohoBooksCommonFunc'
import ZohoBooksFieldMap from './ZohoBooksFieldMap'
import ZohoBooksSalesActions from './ZohoBooksSalesActions'
const isPro = typeof bit_wc_zoho_books !== 'undefined' && bit_wc_zoho_books.isPro


export default function ZohoBooksIntegLayout({ handleInput, wcBooksConf, setWcBooksConf, isLoading, setisLoading, setSnackbar }) {
  const wcCheckoutFieldOptions = () => wcBooksConf?.default?.allFields && Object.keys(wcBooksConf.default.allFields.wcCheckoutFields).map((checkoutKey, indx) => {
    const groupObj = {
      title: checkoutKey, type: 'group', emptyDataLabel: 'No Group Option Found'
    }

    const childs = wcBooksConf.default.allFields.wcCheckoutFields[checkoutKey]

    if (Array.isArray(childs)) {
      groupObj.childs = childs.map(childKey => ({ label: `${checkoutKey} - ${childs[childKey].label}`, value: childKey }))
    } else if (typeof childs === 'object') {
      groupObj.childs = Object.keys(childs).map(childKey => ({ label: `${checkoutKey} - ${childs[childKey].label}`, value: childKey }))
    } else {
      groupObj.childs = []
    }
    if (indx === Object.keys(wcBooksConf.default.allFields.wcCheckoutFields).length - 1) {
      groupObj.childs.push({ label: 'Custom Value', value: 'custom' })
    }
    return groupObj
  })



  return (
    <>
      <br />
      <b className="wdt-100 d-in-b">Organization:</b>
      <select onChange={handleInput} name="orgId" value={wcBooksConf.orgId} className="btcd-paper-inp w-7">
        <option value="">Select Portal</option>
        {
          wcBooksConf?.default?.organizations && Object.values(wcBooksConf.default.organizations).map(organization => (
            <option key={organization.orgId} value={organization.orgId}>
              {organization.orgName}
            </option>
          ))
        }
      </select>
      <button onClick={() => refreshOrganizations(wcBooksConf, setWcBooksConf, setisLoading, setSnackbar)} className="icn-btn sh-sm ml-2 mr-2 tooltip" style={{ '--tooltip-txt': '"Refresh Organization"' }} type="button" disabled={isLoading}>&#x21BB;</button>
      <br />
      <br />
      {wcBooksConf?.orgId &&
        ['booksCustomerFields', 'booksSalesFields'].map(fldType =>
          <div key={fldType} className="pos-rel">
            {!isPro && fldType === 'booksSalesFields' && (
              <div className="pro-blur flx" style={{ top: -25, width: '110%' }}>
                <div className="pro">
                  Available On
                  <a href="https://formsintegrations.com/woocommerce-and-zoho-books-integration/" target="_blank" rel="noreferrer"><span className="txt-pro"> Premium</span></a>
                </div>
              </div>
            )}
            <div className="mt-4">
              <b className="wdt-100">Map Fields (Zoho Books {fldType === 'booksCustomerFields' ? 'Customer' : 'Sales Order'})</b>
              <button onClick={() => refreshFields(wcBooksConf, setWcBooksConf, setisLoading, setSnackbar,fldType)} className="icn-btn sh-sm ml-2 mr-2 tooltip" style={{ '--tooltip-txt': '"Refresh Fields"' }} type="button" disabled={isLoading}>&#x21BB;</button>
            </div>
            <div className="btcd-hr mt-1" />
            <div className="flx flx-around mt-2 mb-1">
              <div className="txt-dp"><b>WooCommerce Checkout Fields</b></div>
              <div className="txt-dp"><b>Zoho Books Fields</b></div>
            </div>

            {wcBooksConf.field_map?.[fldType]?.map((itm, i) => (
              <ZohoBooksFieldMap
                key={`desk-m-${i + 9}`}
                i={i}
                type={fldType}
                field={itm}
                wcBooksConf={wcBooksConf}
                setWcBooksConf={setWcBooksConf}
                wcCheckoutFieldOptions={wcCheckoutFieldOptions}
              />
            ))}
            <div className="txt-center  mt-2" style={{ marginRight: 85 }}><button onClick={() => addFieldMap(wcBooksConf.field_map[fldType].length, fldType, wcBooksConf, setWcBooksConf)} className="icn-btn sh-sm" type="button">+</button></div>
            <br />
            <br />
            <div className="btcd-hr mt-1" />
            {fldType === 'booksSalesFields' && (
              <div className='flx'>
                <ZohoBooksSalesActions
                  wcBooksConf={wcBooksConf}
                  setWcBooksConf={setWcBooksConf}
                  isLoading={isLoading}
                  setisLoading={setisLoading}
                  setSnackbar={setSnackbar}
                />

              </div>


            )}

          </div>
        )}
      <br />
    </>
  )
}
