/* eslint-disable no-undef */
import React, { useEffect, useState } from 'react'
import 'react-multiple-select-dropdown-lite/dist/index.css'
import {
  BrowserRouter as Router, Link, NavLink, Route, Switch
} from 'react-router-dom'
import bitFetch from './components/Childs/bitFetch'
import ImportData from './pages/ImportData'
import LogTable from './pages/LogTable'
import ZohoBooks from './pages/ZohoBooks'
import logo from './resources/icons/logo.svg'
import './resources/sass/app.scss'

function App() {
  const [isAuthorized, setisAuthorized] = useState(false)
  const [wcStatus, setWcStatus] = useState({})
  const [checkingPrevInteg, setCheckingPrevInteg] = useState(false)
  const [wcBooksConf, setWcBooksConf] = useState({
    name: 'Zoho Books API',
    enabled: true,
    clientId: '',
    clientSecret: '',
    orgId: '',

    field_map: {
      booksCustomerFields: [{ formField: '', zohoFormField: '' }],
      booksSalesFields: [{ formField: '', zohoFormField: '' }]
    },
    newInteg: true,
    enableAuthorizationPage: false,
    actions: {
      booksCustomer: {},
      booksSales: {
        warehouseId: '',
      }
    }
  })
  useEffect(() => {
    setCheckingPrevInteg(true)
    bitFetch({}, 'bit_wcb_get_integration_data')
      .then(result => {
        let integDetails = JSON.parse(JSON.stringify(wcBooksConf))
        if (result?.success) {
          if (result.data.integ.length) {
            integDetails = JSON.parse(result.data.integ[0].integration_details)
            setWcBooksConf(integDetails)
            setisAuthorized(true)
          }
          if (result.data.wc_status) setWcStatus(result.data.wc_status)
        }
        setCheckingPrevInteg(false)
      })
  }, [])


  return (
    <Router basename={typeof bit_wc_zoho_books !== 'undefined' ? bit_wc_zoho_books.baseURL : '/'}>
      <div id="btcd-app">

        <>
          <div className="nav-wrp">
            <div className="flx">
              <div className="logo flx" title="WooCommerce Zoho Books">

                <Link to="/" className="flx">
                  <img src={logo} alt="logo" className="ml-2" />
                  <span className="ml-2">Home</span>
                </Link>
              </div>
              <nav className="top-nav ml-2">
                <NavLink
                  to="/import-data"
                  activeClassName="app-link-active"
                >
                  Import to Zoho
                </NavLink>
                <NavLink
                  to="/log-table"
                  activeClassName="app-link-active"
                >
                  All Logs
                </NavLink>
              </nav>
         
              <div className="pro-link">
                <a href='https://formsintegrations.com/woocommerce-and-zoho-books-integration/' target="_blank" rel='noreferrer'>Get Pro</a>
              </div>
            </div>
          </div>
          <Switch>
            <Route exact path="/">
              <ZohoBooks
                isAuthorized={isAuthorized}
                setisAuthorized={setisAuthorized}
                wcBooksConf={wcBooksConf}
                setWcBooksConf={setWcBooksConf}
                checkingPrevInteg={checkingPrevInteg}
              />

            </Route>
            <Route path="/import-data">
              <ImportData
                wcStatus={wcStatus}
              />
            </Route>
            <Route path="/log-table">
              <LogTable />
            </Route>
          </Switch>
        </>




      </div>
    </Router >
  )
}

export default App
