import bitFetch from './Childs/bitFetch'

export const handleInput = (e, wcBooksConf, setWcBooksConf, setisLoading, setSnackbar, isNew, error, setError) => {
  let newConf = { ...wcBooksConf }
  if (isNew) {
    const rmError = { ...error }
    rmError[e.target.name] = ''
    setError({ ...rmError })
  }
  newConf[e.target.name] = e.target.value

  switch (e.target.name) {
    case 'orgId':
      newConf = portalChange(newConf, setWcBooksConf, setisLoading, setSnackbar)
      getWarehouse(newConf,setWcBooksConf)
      getSalespersons(newConf,setWcBooksConf)
      break;
    default:
      break;
  }
  setWcBooksConf(newConf)
}

export const getWarehouse = (wcBooksConf, setWcBooksConf) => {
  const requestParams = { orgId: wcBooksConf.orgId,token:wcBooksConf.tokenDetails.access_token, dataCenter:wcBooksConf.dataCenter }
  bitFetch(requestParams, 'bit_wcb_zoho_books_get_warehouse').then(
    (result) => {
      setWcBooksConf(oldConf => {
        const newConf = { ...oldConf }
        if(result.data.code === 0 && result.data.warehouses) {
         newConf.default = { ...newConf.default, warehouses: result.data.warehouses }
        }else{
          newConf.default = { ...newConf.default, warehouses:[] }
        }
        return newConf
      })
    }
    )
}

export const getSalespersons = (wcBooksConf, setWcBooksConf) => {
  const requestParams = { orgId: wcBooksConf.orgId,token:wcBooksConf.tokenDetails.access_token, dataCenter:wcBooksConf.dataCenter }
  bitFetch(requestParams, 'bit_wcb_zoho_books_get_salespersons').then(
    (result) => {
      setWcBooksConf(oldConf => {
        const newConf = { ...oldConf }
        if(result.data.code === 0 && result.data.data) {
         newConf.default = { ...newConf.default, salespersons: result.data.data }
        }else{
          newConf.default = { ...newConf.default, salespersons:[] }
        }
        return newConf

      })
    }
  )
}

export const portalChange = (wcBooksConf, setWcBooksConf, setisLoading, setSnackbar) => {
  const newConf = { ...wcBooksConf }
  newConf.field_map = {
    booksCustomerFields: [{ formField: '', zohoFormField: '' }],
    booksSalesFields: [{ formField: '', zohoFormField: '' }]
  }
  newConf.actions = {
    booksCustomer: {},
    booksSales: {}
  }

  if (!newConf?.default?.allFields) {
    refreshFields(newConf, setWcBooksConf, setisLoading, setSnackbar)
  } else newConf.field_map = generateMappedField(newConf)
  return newConf
}

export const refreshWarehouse = (wcBooksConf, setWcBooksConf, setisLoading, setSnackbar)=>{
  const newConf = {...wcBooksConf}
  getWarehouse(newConf,setWcBooksConf)
  getSalespersons(newConf,setWcBooksConf)
  setSnackbar({ show: true, msg: 'Warehouse refreshed' })

}
export const refreshOrganizations = (wcBooksConf, setWcBooksConf, setisLoading, setSnackbar) => {
  setisLoading(true)
  const refreshOrganizationsRequestParams = {
    dataCenter: wcBooksConf.dataCenter,
    clientId: wcBooksConf.clientId,
    clientSecret: wcBooksConf.clientSecret,
    tokenDetails: wcBooksConf.tokenDetails,
  }
  bitFetch(refreshOrganizationsRequestParams, 'bit_wcb_refresh_organizations')
    .then(result => {
      if (result && result.success) {
        const newConf = { ...wcBooksConf }
        if (result.data.organizations) {
          newConf.default = { ...newConf.default, organizations: result.data.organizations }
        }
        if (result.data.tokenDetails) {
          newConf.tokenDetails = result.data.tokenDetails
        }
        setSnackbar({ show: true, msg: 'Organizations refreshed' })
        setWcBooksConf({ ...newConf })
      } else if ((result && result.data && result.data.data) || (!result.success && typeof result.data === 'string')) {
        setSnackbar({ show: true, msg: `Organizations refresh failed Cause:${result.data.data || result.data}. please try again` })
      } else {
        setSnackbar({ show: true, msg: 'Organizations refresh failed. please try again' })
      }
      setisLoading(false)
    })
    .catch(() => setisLoading(false))
}

export const refreshFields = (wcBooksConf, setWcBooksConf, setisLoading, setSnackbar,fldType) => {
  setisLoading(true)
  const refreshOrganizationsRequestParams = {
    orgId: wcBooksConf.orgId,
    dataCenter: wcBooksConf.dataCenter,
    clientId: wcBooksConf.clientId,
    clientSecret: wcBooksConf.clientSecret,
    tokenDetails: wcBooksConf.tokenDetails,
  }
  bitFetch(refreshOrganizationsRequestParams, 'bit_wcb_refresh_fields')
    .then(result => {
      if (result && result.success) {
        setWcBooksConf(oldConf => {
          const newConf = { ...oldConf }
          newConf.default = {
            ...newConf.default,
            allFields: {
              wcCheckoutFields: result.data.wcCheckoutFields,
              booksCustomerFields: result.data.booksCustomerFields,
              booksSalesFields: result.data.booksSalesFields
            }
          }
  
          const generatedFields = generateMappedField(newConf)
          if (fldType && newConf.field_map[fldType].length) {
            const element = new Set(newConf.field_map[fldType].map(d => d.zohoFormField))
            const filtered = [...newConf.field_map[fldType], ...generatedFields[fldType].filter(d => !element.has(d.zohoFormField))]
            newConf.field_map[fldType] = filtered
          } else {
            newConf.field_map = generateMappedField(newConf)
          }
          if (result.data.tokenDetails) {
            newConf.tokenDetails = result.data.tokenDetails
          }
          return newConf
        })
        setSnackbar({ show: true, msg: 'WooCommerce Checkout Fields refreshed' })
      } else if ((result && result.data && result.data.data) || (!result.success && typeof result.data === 'string')) {
        setSnackbar({ show: true, msg: `WooCommerce Checkout Fields refresh failed Cause:${result.data.data || result.data}. please try again` })
      } else {
        setSnackbar({ show: true, msg: 'WooCommerce Checkout Fields refresh failed. please try again' })
      }
      setisLoading(false)
    })
    .catch(() => setisLoading(false))
}

export const generateMappedField = wcBooksConf => {
  const fldMap = {}
  const fldTypes = ['booksCustomerFields', 'booksSalesFields']
  fldTypes.map(fldType => {
    fldMap[fldType] = wcBooksConf.default.allFields[fldType].required.length > 0 ? wcBooksConf.default.allFields[fldType].required?.map(field => ({ formField: '', zohoFormField: field })) : [{ formField: '', zohoFormField: '' }]
  })
  return fldMap
}

export const checkMappedFields = wcBooksConf => {

  const isNotMapped = ['booksCustomerFields', 'booksSalesFields'].map(fldType =>
    wcBooksConf?.field_map?.[fldType] ? wcBooksConf.field_map[fldType].filter(mappedField => (!mappedField.formField && mappedField.zohoFormField && wcBooksConf?.default?.allFields?.[fldType]?.required.indexOf(mappedField.zohoFormField) !== -1)) : []
  )
  if (isNotMapped[0].length || isNotMapped[1].length) {
    return false
  }
  return true
}
